<template>
    <div class="card mb-3">
        <div class="card-body">
            <h5 class="card-title mb-2 list-title"><i class="far fa-calendar-plus"></i> Add Todo</h5>
            <form @submit.prevent="onSubmit">
                <div class="input-group mb-3">
                    <input type="text" class="form-control" placeholder="Add Todo..." required v-model="title" />
                    <button class="btn btn-outline-success" type="submit" id="button-addon2"><i class="fas fa-plus-circle"></i></button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    name: "AddTodo",
    data() {
        return{
            title: ''
        }
    },
    methods:{
        onSubmit() {
            this.$emit('add-new-todo', this.title)
            this.title = ''
        }
    }
}
</script>