<template>
    <div class="card mb-3 bg-success text-light">
        <div class="card-header">
            <i class="fas fa-user-circle"></i> Hi, User You can add Todo
        </div>
        <div class="card-body d-flex justify-content-center">
            <span class="todo-button btn btn-danger" @click="showAdd = !showAdd" v-if="showAdd"><i class="fas fa-times"></i></span>
            <span class="todo-button btn btn-primary" @click="showAdd = !showAdd" v-else><i class="fas fa-plus"></i></span>
        </div>
    </div>
    <add-todo v-show="showAdd" @add-new-todo="addTodo" />
    <Spinner v-if="isLoading" />
    <todo-list :todos="todos" @delete-todo="delTodo" @complete-todo="comTodo" v-else />
</template>

<script>
import AddTodo from './AddTodo.vue'
import Spinner from './Spinner.vue'
import TodoList from './TodoList.vue'
export default {
    components: { TodoList, AddTodo, Spinner },
    name: "Main",
    data(){
        return{
            showAdd: false,
            isLoading: true,
            todos: []
        }
    },
    methods:{
        addTodo(title) {
            fetch('https://vrn-todos.herokuapp.com/api/todos', {
                method: 'POST',
                headers:{
                    "Content-Type": "application/json"
                },
                body:JSON.stringify({title})
            })
            .then(res => res.json())
            .then(data => this.todos = [data.data, ...this.todos])

        },
        delTodo(id) {
            console.log(id)
            fetch(`https://vrn-todos.herokuapp.com/api/todos/${id}`, {
                method: "DELETE",
                headers:{
                    "Content-Type": "application/json"
                }
            }).then(res => res.json())
                .then(data => {
                    if(data.success === true){
                        this.todos = this.todos.filter(todo => todo._id !== id)
                    }
                })
            
        },
        comTodo(data) {

            fetch(`https://vrn-todos.herokuapp.com/api/todos/${data.id}`, {
                method: 'PUT',
                headers:{
                    "Content-Type": "application/json"
                },
                body:JSON.stringify({complete:!data.complete})
            })
        }
    },
    mounted(){
        fetch('https://vrn-todos.herokuapp.com/api/todos').then(res => res.json()).then(data => {
            this.todos = data.data
            this.isLoading = false
            })
    }
}
</script>
<style>
.list-title{
    color: #3FB27F;
}
.todo-button {
    border-radius: 100%;
    width: 51px;
    height: 51px;
    text-align: center;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
}
</style>