<template>
    <div class="card">
        <div class="card-body">
            <h5 class="card-title mb-2 list-title"><i class="fas fa-clipboard-list"></i> Todos List</h5>

            <div class="my-3">
                <ul class="list-group"  v-if="todos.length > 0">
                    <li class="list-group-item d-flex justify-content-between align-items-center" v-for="todo in todos" :key="todo._id">
                        <span>
                            <input class="form-check-input me-1" type="checkbox" value="" :checked="todo.complete" @change="$emit('complete-todo', {id:todo._id, complete:todo.complete})" />
                            {{todo.title}}   <span class="badge bg-dark mx-3"><i class="fas fa-calendar-alt"></i> {{new Date(todo.date).toLocaleDateString('en-eg', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' })}}</span>
                        </span>
                        <span>
                            <button type="button" class="btn btn-outline-danger btn-sm" @click="$emit('delete-todo', todo._id)"><i class="fas fa-trash-alt"></i></button>
                        </span>
                    </li>
                </ul>
                <div class="alert alert-success" v-else>there is no todos :)</div>
            </div>
            
            <!-- <a href="#" class="card-link">Card link</a>
            <a href="#" class="card-link">Another link</a> -->
        </div>
    </div>
</template>

<script>
export default {
    name: "TodoList",
    props: ['todos']
}
</script>